.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.logo {
  width: 150px;
}

.nav-items {
  padding: 0px 20px;
}

.nav-items > ul {
  font-size: 25px;
  display: flex;
  list-style-type: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}

.res-container {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}

.res-card {
  margin: 8px;
  width: 300px;
  height: 420px;
  padding: 5px;
  background-color: #d3d3d3;
}

.res-card:hover {
  border: 1px solid black;
  cursor: pointer;
}

.filter {
  padding: 10px;
  display: flex;
  align-items: center;
}

.res-logo {
  width: 100%;
}

.filter-btn {
  margin: 10px;
  cursor: pointer;
}

.shimmer-container {
  display: flex;
  flex-wrap: wrap;
}

.shimmer-card {
  margin: 20px;
  width: 200px;
  height: 300px;
  background-color: #f0f0f0;
}

.login-btn {
  padding: 0 20px;
  width: 70px;
  height: 50px;
  cursor: pointer;
}

.serch-box {
  margin-left: 10px;
}
